.main {
  display: flex;
  flex-direction: column;
  margin-top: 64px;
  position: relative;
  width: 100%;
  z-index: var(--z-main);
}

/* ------------------------ */
/* GRADIENT (under header)
============================*/
.gradient,
.gradient-overlay {
  height: 100%;
  position: absolute;
  width: 100%;
}

.gradient-overlay {
  left: 0;
  top: 0;
}

.gradient {
  background: conic-gradient(
    from 180deg at 50% 50%, #2a8af6 0deg, #a853ba 180deg, #e92a67 1turn
  );
  border-radius: 100%;
  filter: blur(75px);
  left: 50%;
  margin: 0 auto;
  min-height: 800px;
  mix-blend-mode: normal;
  opacity: .4;
  pointer-events: none;
  right: 50%;
  top: -400px;
  max-width: 1000px;
  max-height: 800px;
  transform: translateX(-50%);
  user-select: none;
  will-change: filter;
}
/* ------------------------ */


.scroll-top {
  height: 36px;
  width: 36px;
  border-radius: 24px;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  transition: all 150ms ease-in-out;
  display:flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index:9999;
  background: rgba(57, 57, 57, 0.2);
  box-shadow: var(--bs-md);
}
.hide-scroll-top {
  z-index: -1;
  pointer-events: none;
  user-select: none;
  opacity: 0;
  background: transparent;
}

.scroll-top svg {
  height: 26px;
  width: 26px;
}

.scroll-top:hover {
  background: var(--black2);
}

/* ------------------------ */
/* SECTIONS
============================*/
.projects-container,
.skills-container,
.about-container,
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  padding: 0 1.5rem 0 1.5rem;
  position: relative;
  background: linear-gradient(180deg, transparent 75%, rgb(0, 0, 0) 90%);
  z-index: 3;
}

.skills-container,
.about-container,
.contact-container {
  margin-top:-16px;
  padding-bottom: 6rem;
}

.projects-grid,
.skills-grid,
.contact-grid,
.about-grid {
  display: grid;
  column-gap: 1rem;
  row-gap: 1.5rem;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.section-title {
  display: flex;
  padding-top: 4rem;
  flex-direction: column;
  row-gap: 1.5rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding-bottom: 2.5rem;
}

.section-title h2 {
  font-family: var(--primaryFont);
  font-weight: bold;
  font-size: 2.25rem;
}

.section-subtitle {
  font-family: var(--secondaryFont);
  line-height: 1.375;
  color: var(--white3);
}

/* ------------------------ */
/* SECTION DIVIDER
============================*/
.section-divider {
  min-width: 100vw;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  min-height: 2px;
  background: rgba(168, 83, 186, .1);
  backdrop-filter: brightness(2.2);
  position: relative;
  z-index: 6;
}

.section-gradient-left,
.section-gradient-right {
  margin-top: -8px;
  border-radius: 100%;
  filter: blur(48px);
  min-height: 16px;
  width: 100%;
  opacity: .9;
  pointer-events: none;
  user-select: none;
  mix-blend-mode: normal;
}

.section-gradient-left {
  background: var(--primary1);
  transform: translateX(-65%);
  position: relative;
  z-index: 2;
}

.section-gradient-right {
  background: var(--red1);
  position: relative;
  z-index: 2;
  transform: translateX(65%);
}
/* ------------------------ */


/* ------------------------ */
/* ------------------------ */
/* SECTION: PROJECTS
============================*/
.projects-container {
  min-height: calc(100vh - 64px);
}

.projects-header {
  min-height: 460px;
  width: 100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position:relative;
}

.lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 280px;
  max-width: 580px;
  margin: 0 auto;
  border-bottom: 1px solid #4f4f4f;
  z-index: 1;
  background: transparent;
  background-size: calc(100% / 4) 4%;
  background-image:
  linear-gradient(to right, var(--mdgrey1) .5px, transparent 1px),
  linear-gradient(to left, var(--mdgrey1) .5px, transparent 1px); 
}

.projects-header__title {
  font-size: 5rem;
  font-family: var(--primaryFont);
  position: relative;
  z-index: 2;
  padding-bottom: 1.25rem;
}

.projects-header__subtitle {
  font-size: 1.125rem;
  color: var(--white2);
  font-family: var(--secondaryFont);
  line-height: 1.375;
}

.projects-grid {
  padding: 1rem 0 6rem 0;
  grid-template-columns: 1fr 1fr;
}

.project-cell {
  min-height: 380px;
  background: rgba(53, 52, 52, 0.1);
  border-radius: .75rem;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;
  gap: .5rem;
  text-align: center;
}

/* ------------------------ */
/* ------------------------ */
/* SECTION: SKILLS
============================*/
.skills-grid {
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
}

.skills-cell {
  border: 1px solid var(--darkgrey3);
  border-radius: .75rem;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;
  gap: .5rem;
}

/* ------------ */
/* skills-grid: header */
.skill__header {
  height: 64px;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

.skill__header--icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 64px;
  width: 64px;
  border: 1.5px solid var(--black3);
  border-radius: 32px;
}

.skill__header--icon {
  align-items: center;
  border-radius: 30px;
  display: flex;
  height: 48px;
  justify-content: center;
  overflow: hidden;
  position: relative;
  width: 48px;
}

.skill__header--icon:after,
.skill__header--icon:before {
  border-radius: 50%;
  content: "";
  position: absolute;
}

.skill__header--icon:after {
  background: var(--black0);
  height: 46px;
  margin: 0 auto;
  width: 46px;
  z-index: 2;
}

.skill__header--icon:before {
  border: 16px solid transparent;
  border-image: linear-gradient(to right bottom, #656565, #262626, #262626);
  border-image-slice: 1;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  transition: background-image 150ms ease-in-out;
}

.skill__header--icon:hover:after {
  background: var(--black0);
}

.skill__header--icon:hover:before {
  border-image: linear-gradient(to right bottom, #e92a67, #1e1e1e, #080808);
  border-image-slice: 1;
}

.skill__header--icon:hover svg {
  fill: #e92a67;
}

.skill__header--icon svg {
  fill: url(#subicon-gradient);
  border-radius: 50%;
  filter: drop-shadow(0 0 3px rgba(233, 42, 103, .5));
  height: 42px;
  width: 42px;
  position: relative;
  transition: fill 150ms ease-in-out;
  z-index: 3;
}

/* ------------ */
/* skills: body */
.skill__body {
  display: flex;
  flex-direction: column;
  gap: .5rem;
  padding-top: .5rem;
}

.skill__body--title h3 {
  font-family: var(--secondaryFont);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.5rem;
  color: var(--white1);
}

.skill__body--content {
  color: var(--white3);
  line-height: 1.5rem;
  font-weight: 500;
}

